<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  inject: ['reload'],
  data () {
    return {
      columns: [
          {
            title: 'ID',
            key: 'id',
            align: 'center',
            // fixed: 'left'
          },
          {
            title: '名称',
            key: 'name',
            align: 'center',
          },
          {
            title: '关联游戏',
            key: 'similar_sonName',
            align: 'center',
          },
          {
            title: '类型',
            key: 'type_name',
            align: 'center',
          },
          {
            title: '添加时间',
            key: 'formatTime',
            align: 'center',
          },
          {
            title: '编辑(添加人/最后修改人)',
            key: 'mergeAdmin',
            align: 'center',
          },
          {
            title: '标签',
            key: 'tagStr',
            align: 'center',
          },
          {
            title: '状态',
            key: 'state',
            align: 'center',
            render: (h, params) => {
              const status = params.row.state;
              const color = status == 1 ? 'success' : status == 2 ? 'info' : status == 3 ? 'error' : 'warning';
              const text = status == 1 ? '正常' : status == 2 ? '下架' : status == 3 ? '删除' : '-';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color,
                }
              }, text);
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                  h('Button', {
                    props: {
                      type: 'info',
                      size: 'small'
                    },
                    style: {
                      margin: '0 4px 2px 0',
                    },
                    on: {
                      click: () =>{
                        this.$router.push({
                          path: '/data/editallgame',
                          query: {
                            'id': params.row.id
                          }
                        })
                      }
                    },
                  }, '编辑'),
                  h('Button', {
                    props: {
                      type: 'warning',
                      size: 'small'
                    },
                    style: {
                      margin: '0 4px 2px 0'
                    },
                    on: {
                      click: () =>{
                        this.$router.push({
                          path: '/data/editNewGame',
                          query: {
                            'id': params.row.id
                          }
                        })
                      }
                    },
                  }, '添加游戏')
              ]);
            }
          }
      ],
      data: [],
      datePreSet: {
        shortcuts: [
          {
            text: '现在',
            value () {
              let d = new Date(),
                  str = '';
                  str += d.getFullYear() + '-';
                  str += d.getMonth() + 1 + '-';
                  str += d.getDate() + ' ';
                  str += d.getHours() + ':';
                  str += d.getMinutes() + ':';
                  str += d.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click today');
            }
          },
          {
            text: '昨天',
            value () {
              const date = new Date();
                    date.setTime(date.getTime() - 3600 * 1000 * 24);
              let str = '';
                  str += date.getFullYear() + '-';
                  str += date.getMonth() + 1 + '-';
                  str += date.getDate() + ' ';
                  str += date.getHours() + ':';
                  str += date.getMinutes() + ':';
                  str += date.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click yesterday');
            }
          },
          {
            text: '一周前',
            value () {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              let str = '';
                  str += date.getFullYear() + '-';
                  str += date.getMonth() + 1 + '-';
                  str += date.getDate() + ' ';
                  str += date.getHours() + ':';
                  str += date.getMinutes() + ':';
                  str += date.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click a week ago');
            }
          }
        ]
      },
      formData: {
        seltype: 'name',
        selval: '',
        stime: '',
        etime: '',
        adminid: '0',
        show_down: '0',
      },
      adminList: {},
      totalPages: 0,
      isLoading: true,
    }
  },
  mounted () {
    let _this = this
    this.isLoading = false
    const token = getToken(),
          page = 1,
          per_page = 10,
          seltype = 'name',
          selval = '',
          stime = '',
          etime = '',
          adminid = 0,
          show_down = 0,
          params = {token, page, per_page, seltype, selval, stime, etime, adminid, show_down};

    this.params = params;
    this.getData();

    document.onkeydown = function () {// 监听enter事件
      let key = window.event.keyCode;
      if (key === 13) { 
        _this.handleForm()
      }
    }
  },
  methods: {
    handleForm () {
      const token = getToken(),
          page = this.params.page*1,
          per_page = this.params.per_page*1,
          seltype = this.formData.seltype,
          selval = this.formData.selval,
          stime = this.formData.stime,
          etime = this.formData.etime,
          adminid = this.formData.adminid,
          show_down = this.formData.show_down,
          params = {token, page, per_page, seltype, selval, stime, etime, adminid, show_down};

      console.log(this.formData.stime)
      this.params = params;
      this.getData();
    },
    getData () {
      this.isLoading = true;
      api.allGame(this.params).then(res => {
        // console.log(res)
        this.isLoading = false
        if(res.code == 0) {
          this.adminList = res.data.adminlist
          this.totalPages = res.data.totlerows
          this.data = res.data.gamelist
          this.data.forEach(ele => {
            //类型
            ele.type_name = ele.type_name == 0 ?  '-' : ele.type_name
            //关联标签名称
            if(ele.similar_son) {
              ele.similar_sonName = '';
              if(ele.similar_son.length == 1) {
                ele.similar_sonName = ele.similar_son[0].name
              } else {
                ele.similar_son.forEach(ele2 => {
                  ele.similar_sonName += (ele2.name+" | ")
                })
              }
            } else {
              ele.similar_sonName = '-'
            }
             
            //标签调整
            if(ele.tagLists) {
              if (ele.tagLists.length == 1) {
                ele.tagStr = ele.tagLists[0].name;
              } else if(ele.tagLists.length>0){
                ele.tagStr = '';
                ele.tagLists.forEach(itm => {
                  ele.tagStr += (itm.name+' | ')
                });
              } else {
                ele.tagStr = '-'
              }
            } else {
              ele.tagStr = '-'
            }
            
            //格式化时间
            ele.formatTime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
            //添加修改人整合
            ele.mergeAdmin = (ele.addadmin_name ? ele.addadmin_name : '') + ' / ' + (ele.upadmin_name ? ele.upadmin_name : '');
          })
        } else {
          this.$Notice.error({
           desc: res.msg
          })
        }
      }).catch(err => {
        this.$Notice.error({
          desc: err
        })
      })
    },
    changePage (page) {
      // console.log(page)
      this.params.page = page;
      this.handleForm();
    },
    changePageNum (num) {
      // console.log(num)
      this.params.page = 1;
      this.params.per_page = num;
      this.handleForm();
    },
    getSTime (t) {
      this.formData.stime = t;
    },
    getETime (t) {
      this.formData.etime = t;
    }
  }
}
</script>
<template>
  <div>
    <h3><Icon type="ios-home" size=24 />主游戏列表</h3>
    <div class="formBox">
      <Button type="info" to="/data/editAllGame" style="width:110px">添加主游戏<Icon type="ios-nutrition" size=14 /></Button>
      <Select v-model="formData.seltype" style="width:100px">
        <Option value="name">名称</Option>
        <Option value="id">ID</Option>
      </Select>
      <Input v-model="formData.selval" clearable style="width: 200px" />
      <DatePicker v-model="formData.stime" @on-change="getSTime" type="datetime" :options="datePreSet" placeholder="选择开始日期" style="width: 200px"></DatePicker>
      <DatePicker v-model="formData.etime" @on-change="getETime" type="datetime" :options="datePreSet" placeholder="选择结束日期" style="width: 200px"></DatePicker>
      <Select v-model="formData.adminid" style="width:100px" @on-change="handleForm">
        <Option value="0">管理员</Option>
        <Option v-for="itm,idx in adminList" :value="itm.uid" :key="idx">{{itm.username}}</Option>
      </Select>
      <Select v-model="formData.show_down" style="width:120px" @on-change="handleForm">
        <Option value="0">下载聚合页</Option>
        <Option value="1">是</Option>
        <Option value="2">否</Option>
      </Select>
      <Button type="primary" style="width:100px" @click="handleForm">
        <Icon type="ios-search" size=16></Icon>
        搜索
      </Button>
    </div>
    <Table 
      border 
      ref="selection" 
      :columns="columns" 
      :data="data" 
      :loading=isLoading
      >
    </Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" show-total show-elevator show-sizer :page-size=10 @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .formBox {
    .ivu-select {
      margin: 0 20px 20px 0;
    }
    .ivu-input-wrapper {
      margin: 0 20px 20px 0;
    }
    .ivu-date-picker, .ivu-btn {
      position: relative;
      vertical-align: middle;
      line-height: normal;
      margin: 0 20px 20px 0;
    }
    .ivu-select{
      width: 160px;
    }
  }
</style>
